@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu {
  background-color: #00fdc8;
  color: #000;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-size: 22px;
  text-align: center; }
  .menu .nav-item {
    display: inline;
    padding: 0 5px;
    margin: 2px; }
    .menu .nav-item a {
      color: #000;
      text-decoration: none;
      padding: 0 2px; }
      .menu .nav-item a:hover {
        text-decoration: underline; }
      .menu .nav-item a.active, .menu .nav-item a.active:hover {
        background-color: #000;
        border-radius: 3px;
        text-decoration: none;
        color: #fff; }
      .menu .nav-item a.homelink {
        color: #000;
        background-color: #00fdc8;
        text-decoration: none; }

body {
  background-color: #fff;
  color: #555;
  font-family: 'Source Code Pro', monospace; }

h1, h2, h3, h4, h5, h6, header {
  font-family: 'Amatic SC', sans-serif;
  color: #000; }

.todo {
  font-family: 'Courier New', Courier, monospace;
  color: #0ff;
  background-color: #000; }

.no-data {
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  color: #f00;
  background-color: transparent; }

.loading {
  color: #ff0;
  font-weight: bold; }

.todo::before {
  content: 'TODO: '; }

.content {
  margin: 10px 10%;
  padding: 10px;
  border: inset thin #ccc;
  min-height: 75vh; }
  .content header {
    font-size: 3em;
    text-align: center;
    margin: 1em 2em; }

.page-container {
  position: relative;
  min-height: 100vh; }

.content-wrapper {
  padding-bottom: 2rem; }

.footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  height: 2rem;
  background-color: #000;
  color: #00fdc8;
  padding-top: 5px; }
  .footer .copyright {
    float: left;
    margin-left: 10px; }
  .footer .social {
    float: right;
    margin-right: 10px; }
    .footer .social img {
      height: 1rem;
      margin-right: 5px; }
    .footer .social a {
      color: #fff; }

.home-gallery {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly; }
  .home-gallery .thumbnail {
    margin: 1em; }

.home-bkg {
  background-clip: content-box;
  background-image: url(https://res.cloudinary.com/cantimaginewhy/image/upload/v1571965112/me/caterpillar2.jpg);
  opacity: 0.4;
  height: 100%; }

.site-title {
  position: absolute;
  left: 40%;
  top: 50px;
  z-index: 10;
  font-family: 'Amatic SC';
  font-size: 3em;
  font-weight: bold; }

.about-content {
  display: flex;
  flex-direction: row; }
  .about-content div {
    margin: 0 1em; }
  .about-content .me-photo {
    border: thin inset; }

.gallery-nav {
  background-color: #ddd;
  border-radius: 5px;
  padding: 2px 3px;
  cursor: pointer;
  text-align: center;
  color: #000; }
  .gallery-nav div {
    display: inline;
    margin: 0 5px; }
  .gallery-nav a {
    text-decoration: none;
    color: #111;
    padding: 2px 3px; }
    .gallery-nav a:hover {
      text-decoration: underline; }
    .gallery-nav a.active, .gallery-nav a.active:hover {
      background-color: #333 !important;
      text-decoration: none;
      color: #ddd;
      border-radius: 2px; }

.album-list {
  display: flex;
  flex-direction: row;
  justify-content: center; }

.select-label {
  font-size: 14px; }

.list-title {
  display: block;
  margin: 0 auto;
  text-align: center;
  font-size: 22px; }

.album-btn {
  border: outset thin;
  margin: 2px;
  cursor: pointer; }
  .album-btn .album-name {
    font-size: 18px;
    font-family: 'Amatic SC', sans-serif;
    text-align: center;
    color: #000; }
  .album-btn .album-desc {
    font-size: 10px;
    text-align: left;
    margin: 5px;
    visibility: hidden; }
  .album-btn:hover .album-desc {
    visibility: visible; }

.selected-nav {
  border: inset thin;
  background-color: #00fdc8; }
  .selected-nav .album-name {
    color: #000;
    font-weight: 600; }

.current-nav {
  text-align: center;
  margin: 5px auto;
  color: #000; }
  .current-nav .nav-title {
    font-size: 2em;
    font-weight: 600;
    font-family: 'Amatic SC', sans-serif; }
  .current-nav .nav-description {
    font-size: 1em;
    font-weight: normal;
    font-family: 'Source Code Pro', monospace; }

.gallery-title {
  font-size: 2em;
  font-family: 'Amatic SC', sans-serif;
  font-weight: bold;
  text-transform: capitalize;
  text-align: center; }

.display-area {
  display: flex;
  flex-direction: row; }
  .display-area .gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
    .display-area .gallery .thumbnail {
      display: inline-block;
      margin: 5px;
      cursor: pointer; }
    .display-area .gallery .title {
      display: none;
      font-size: 12px;
      font-family: 'Amatic SC', sans-serif;
      text-align: center; }
  .display-area .image-view {
    margin: 10px 5px; }
    .display-area .image-view .display-image {
      cursor: pointer; }
    .display-area .image-view .image-info {
      border: 1px inset #999;
      font-family: 'Source Code Pro', monospace;
      padding: 3px 5px;
      margin-top: 2px; }
      .display-area .image-view .image-info .title {
        font-size: 12px;
        font-weight: bold; }
      .display-area .image-view .image-info .label {
        font-size: 10px;
        text-align: right; }
      .display-area .image-view .image-info .info {
        font-size: 11px;
        text-align: left;
        font-weight: 500; }
    .display-area .image-view .close-btn {
      font-size: 2em;
      font-family: 'Courier New', Courier, monospace;
      color: black;
      background-color: white;
      padding: 3px;
      border-radius: 3px;
      cursor: pointer; }
    .display-area .image-view .options {
      line-height: 2em;
      margin: 5px 0;
      font-size: 12px; }
    .display-area .image-view .purchase {
      margin: 0 2px;
      padding: 1px 3px;
      border-radius: 2px;
      text-align: center;
      cursor: pointer; }
    .display-area .image-view .buy-orig {
      background-color: #900;
      color: #fff; }
    .display-area .image-view .buy-print {
      background-color: #060;
      color: #fff; }

